
.SearchExtensive .buttonSearch .loadingIcon {
    display: none;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    border: 1px solid #aaa;
    position: absolute;
    right: -17px;
    top: 2px;
    margin: 0;
    border-top-color: transparent;
    -webkit-animation: rotate 1s ease-out 0s infinite forwards normal;
            animation: rotate 1s ease-out 0s infinite forwards normal;
}

.SearchExtensive .buttonSearch.loading .loadingIcon {
    display: block;
}

.SearchExtensive .buttonSearch.loading i.material_icons {
    display: none;
}

@-webkit-keyframes rotate {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes rotate {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.SearchExtensive .SearchInputGroup {
    margin-top: 10px;
    position: relative;
}

.SearchExtensive .SearchInputGroup .loadingIcon.loading {
    display: block;
}

.SearchExtensive .SearchInputGroup button i.search {
  font-size: 20px;
}

.SearchExtensive .SearchInputGroup input {
    width: 100%;
    padding: 0.3em .3em;
    border-color: #946985;
    border-radius: 3px;
    background: none;
    -webkit-box-shadow: none;
            box-shadow: none;
}

.SearchExtensive .SearchInputGroup button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    background: none;
    border-radius: 0;
    color: #946985;

    padding: 0;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    position: absolute;
    right: 25px;
    top: 2px;
    width: 0;
    height: 0;
    margin: 0!important;
}



